<template>
	<h1>
		Reaction timer, <em>BAM!</em>
	</h1>
	<button @click="start" :disabled="isPlaying">GO</button>
	<Block v-if="isPlaying" :delay="delay" @end="endGame" />
	<Results v-if="showScore" :score="score" />
</template>

<script>
import Block from './components/Block.vue';
import Results from './components/Results.vue';

export default {
	name: 'App',
	components: { Block, Results },
	data() {
		return {
			isPlaying: false,
			delay: null,
			score: null,
			showScore: false,
		};
	},
	methods: {
		start() {
			this.delay = 2000 + Math.round(Math.random() * 5000); // so somewhere between 2-7 seconds
			this.isPlaying = true;
			this.showScore = false;
		},
		endGame(reactionTime) {
			// catch 2nd argument (reactionTime) of emitted custom event 'end' from Block.vue
			this.score = reactionTime;
			this.isPlaying = false;
			this.showScore = true;
		},
	},
};
</script>

