<template>
	<div class="backdrop">
		<div class="block" v-if="showBlock" @click="stopTimer">
			{{ showBlock }}
			<div class="inside">Click!
				<i class=small><br />Shown after {{ delay }}ms</i>
			</div>
		</div>
	</div>
</template>
<script>

export default {
	props: ['delay'],
	data() {
		return {
			showBlock: false,
			timer: null,
			reactionTime: 0
		}
	},
	mounted() {
		console.log('mounted')
		setTimeout(() => {
			this.showBlock = true
			this.startTimer()
		}, this.delay)
	},
	methods: {
		startTimer() {
			console.log('starting timer')
			this.timer = setInterval(() => {
				this.reactionTime += 10
			}, 10);
		},
		stopTimer() {
			clearInterval(this.timer);
			console.log('this.reactionTime:', this.reactionTime)
			this.showBlock = false
			this.$emit('end', this.reactionTime)
		}
	},
}
</script>

